<template>
	<view>
		
		
		
		<view @tap="goback" class="cuIcon-roundleftfill-copy" style="position: fixed;color: rgba(100,100,100,0.7);top: 30upx;left: 30upx; font-size: 50upx;z-index: 99;"></view>

		<view class="padding bg-white solid-bottom" style="padding-top: 100upx;">
			<view class="flex p-xs  mb-sm">
				<view class="flex-sub text-black" style="font-size: 40upx;font-weight: bold;">{{goods.title}}</view>
				
			</view>
			<view class="flex justify-start">
					<view class="padding-sm" style="font-size: 46upx;font-weight: bold;color: red;display: table-cell;vertical-align: middle;">{{priceLabel}}</view>
					<view v-if="promoteFlag" class="" style="display: table-cell;vertical-align: middle;">
						<text style="padding:5upx 10upx; color: white;background-color: red;border-radius: 10upx;">促销</text>
					</view>
					<view v-if="promoteFlag" style="display: table-cell;vertical-align: middle;">
						<text style="color: red;font-size: 32upx;">{{promoteLabel}}</text>
					</view>
			</view>
			<view class="flex text-lg text-bold margin-bottom-sm">
				<view class="flex-sub" style="padding-left: 0;">商品规格:</view>
				<view class="flex-treble" style="text-decoration: underline;">
					<picker mode="selector" @change="selectStyle" :value="styleIndex" :range="stylelist">
                        {{goods.style[styleIndex].styleName}}
                    </picker>
				</view>
			</view>
			<view class="flex justify-start margin-bottom-sm">
				<view style="border-radius: 10upx;">
					<image style="height: 40upx;width: 40upx;" src="../../../static/img/dian_small.png"></image>
				</view>
				<view class="text-lg text-bold text-blue">{{goods.seller}}</view>
					
			</view>
			<view class="flex text-lg text-bold margin-bottom-sm" style="display: table; vertical-align: middle;">
				<view style="display: table-cell;vertical-align: middle;width: 30%;">免费</view>
				<view style="display: table-cell;vertical-align: middle;">
					<image style="height: 40upx;width: 40upx;" src="../../../static/img/small_location.png"></image>
				</view>
				<view style="display: table-cell;font-size: 26upx;vertical-align: middle;">
					{{goods.location}}
				</view>
			</view>
			<view  style="border: solid 0.1upx #666;"></view>
			
			<view style="margin: 20upx 0;">
				<block v-for="(detail, index) in detaillist"  v-bind:key="index">
					<text v-if="detail.type == 'text'">{{detail.value}}</text>
					
					<image   v-if="detail.type == 'image'" :src="detail.value" ></image>
				</block>
			</view>
			
			
		</view>
		
		<view class="cu-bar bg-white tabbar border shop fixed " style="top: unset;bottom: 0;z-index: 100;">
				<view class="flex-sub bg-green submit cuIcon-markfill" style="flex: 1;font-size: 60upx;"></view>
				
				<view class="flex-treble bg-red submit">购买</view>
		</view>
		
		<view v-if="tobuy" style="position: fixed;z-index: 101;top: 0;left:0;width: 100%; height: 100%;">
			<view @tap="closeBuyLayer" style="position: absolute;top: 0;left:0;width: 100%; height: 100%;background-color: rgba(100,100,100,0.5);">
				
			</view>
			<view :animation="tobuyAnimation" style="position: absolute;bottom: 0; width: 100%;max-height: 100%;background-color: #fff;">
				<view class="cu-bar bg-red">
					<view class="action" @tap="closeBuyLayer">
						<text class="cuIcon-close"></text>
					</view>
					<view class="content text-bold">
						下单支付
					</view>
				</view>
				<form>
					<view class="cu-form-group margin-top">
						<view class="title">收货人</view>
						<input placeholder="输入收货人姓名" name="input"></input>
					</view>
			
			<view class="cu-form-group">
				<view class="title">手机号</view>
				<input placeholder="输入收货人手机号" type="tel" name="input"></input>
				
			</view>
			
			<view class="cu-form-group">
				<view class="title">地址选择</view>
				<picker mode="multiSelector" @change="selectRegion" @columnchange="regionChange" :value="regionIndex" :range="regionArray">
					<view class="picker">
						{{regionArray[0][regionIndex[0]]}}，{{regionArray[1][regionIndex[1]]}}，{{regionArray[2][regionIndex[2]]}}
					</view>
				</picker>
			</view>
			
			<view class="cu-form-group">
				<textarea maxlength="-1" placeholder="请输入详细地址"></textarea>
			</view>
			
			<view class="cu-form-group align-start">
				<view class="title">备注</view>
				<textarea maxlength="-1" placeholder="你想对卖家说什么"></textarea>
			</view>
			<view class="cu-form-group">
				<view class="title">商品价格</view>
				<text class="text-red text-bold">{{priceLabel}}</text>
			</view>
			<view class="cu-form-group">
				<view class="title">购买数量</view>
				<uni-number-box :min="0" :max="9">{{count}}</uni-number-box>
			</view>
			<view class="cu-form-group">
				<view class="title">运费</view>
				<text class="text-red text-bold">¥{{goods.post_fee}}</text>
			</view>
			<view class="text-center text-xxl padding text-bold text-price text-red">
				{{price*count+goods.post_fee}}
			</view>
			<view class="padding flex flex-direction">
				
				<button class="cu-btn bg-green margin-tb-sm lg">微信支付</button>
			</view>
		</form>
			</view>
		</view>
	</view>
</template>

<script>
	import uniNumberBox from '@dcloudio/uni-ui/lib/uni-number-box/uni-number-box.vue'
	export default {
		components: {uniNumberBox},
		data:function(){
			return {
				goods:{},
				priceLabel:"",
				detaillist:[],
				stylelist:[],
				styleIndex:0,
				promoteFlag:false,
				promoteLabel:'',
				region: ['广东省', '广州市', '海珠区'],
				regionArray:['请选择','请选择','请选择'],
				regionIndex:[0,0,0],
				regionData:[],
				price:0,
				count:1,
				tobuy:true,
				tobuyAnimation:{}
			}
		},
		onLoad:function(option){
			uni.showToast({
				title:JSON.stringify(option)
			});
			let goodsId = option.goodsId;
			uni.request({
				url: 'https://www.dpsoft.top/goods_new_detail.jsp', //仅为示例，并非真实接口地址。
				method:"POST",
				data: {
					goodsid: goodsId
				},
				header: {
					"Content-Type": "application/json"
				},
				success: (res) => {
					console.log(res.data);
					if("ok" == res.data.status){
						this.$data.goods = res.data.data;
						this.promoteFlag = this.goods.promote == 1;
						if(this.goods.price > this.goods.promote_price){
							this.priceLabel = "¥"+this.goods.promote_price + " ~ ¥" + this.goods.max_price;
						}else{
							this.priceLabel = "¥"+this.goods.price + " ~ ¥" + this.goods.max_price;
						}
						this.detaillist = this.goods.detail;
						let styleArray = this.goods.style;
						for(let x in styleArray){
							let style = styleArray[x];
							let styleName = style.styleName;
							let goodsPrice = style.goodsPrice;
							let styleString = styleName+" ¥"+goodsPrice;
							let promotePrice = style.promotePrice;
							if(typeof(promotePrice) != 'undefined' && promotePrice!=null){
								styleString = styleName+" ¥"+promotePrice;
							}
							this.stylelist.push(styleString);
						}
					}
				}
			});
			
			let that = this;
				let data = {
					multiArray: this.regionArray,
					multiIndex: this.regionIndex,
					multiData: this.regionData
				};
			let pcode = '100000';
			uni.request({
				url: 'https://www.dpsoft.top/district.jsp', //仅为示例，并非真实接口地址。
				method:"POST",
				data: {
					code: pcode
				},
				header: {
					"Content-Type": "application/json"
				},
				success: (res) => {
					console.log(res.data);
					if("ok" == res.data.status){
						data.multiData[0] = res.data.data;
						data.multiArray[0] = [];
						for(let x in res.data.data){
							let district = res.data.data[x];
							data.multiArray[0].push(district.name);
						}
						data.multiIndex[1] = 0;
						data.multiArray[1] = ['请选择'];
						data.multiArray[2] = ['请选择'];
						data.multiIndex[2] = 0;
						
						that.regionArray = data.multiArray;
						that.regionIndex = data.multiIndex;
						that.regionData = data.multiData;
					}
				},
			});
		},
		methods:{
			selectStyle: function(e) {
				console.log('选择商品规格为', e.target.value)
				this.styleIndex = e.target.value
				let style = this.goods.style[this.styleIndex];
				if(style.promotePrice){
					
					this.promoteFlag = true;
					let promoteStartTime = style.promoteStarttime;
					let promoteEndTime = style.promoteEndtime;
					let now = Math.floor(Date().getTime()/1000);
					if(now < promoteStartTime){
						this.priceLabel = '¥'+style.goodsPrice;
						this.promoteLabel =  new Date(promoteStartTime*1000).toLocaleDateString()+"开始";
						this.price = style.goodsPrice;
					}else if(now > promoteEndTime){
						this.priceLabel = '¥'+style.goodsPrice;
						this.promoteLabel =  "已结束";
						this.price = style.goodsPrice;
					}else{
						this.priceLabel = '¥'+style.promotePrice;
						this.promoteLabel =  new Date(promoteEndTime*1000).toLocaleDateString()+"截止";
						this.price = style.promotePrice;
					}
					
				}else{
					this.priceLabel = '¥'+style.goodsPrice;
					this.promoteFlag = false;
					this.price = style.goodsPrice;
				}
			},
			RegionChange(e) {
				this.region = e.detail.value
			},
			selectRegion(e) {
				this.multiIndex = e.detail.value;
				
			},
			regionChange(e) {
				let that = this;
				let data = {
					multiArray: this.regionArray,
					multiIndex: this.regionIndex,
					multiData: this.regionData
				};
				data.multiIndex[e.detail.column] = e.detail.value;
				switch (e.detail.column) {
					case 0:{
						let pcode = data.multiData[0][data.multiIndex[0]].code;
						uni.request({
							url: 'https://www.dpsoft.top/district.jsp', //仅为示例，并非真实接口地址。
							method:"POST",
							data: {
								code: pcode
							},
							header: {
								"Content-Type": "application/json"
							},
							success: (res) => {
								console.log(res.data);
								if("ok" == res.data.status){
									data.multiData[1] = res.data.data;
									data.multiArray[1] = [];
									for(let x in res.data.data){
										let district = res.data.data[x];
										data.multiArray[1].push(district.name);
									}
									data.multiIndex[1] = 0;
									data.multiArray[2] = ['请选择'];
									data.multiIndex[2] = 0;
									
									that.regionArray = data.multiArray;
									that.regionIndex = data.multiIndex;
									that.regionData = data.multiData;
								}
							},
						});
						
						
						}
						break;
					case 1:{
						let pcode = data.multiData[1][data.multiIndex[1]].code;
						uni.request({
							url: 'https://www.dpsoft.top/district.jsp', //仅为示例，并非真实接口地址。
							method:"POST",
							data: {
								code: pcode
							},
							header: {
								"Content-Type": "application/json"
							},
							success: (res) => {
								console.log(res.data);
								if("ok" == res.data.status){
									data.multiData[2] = res.data.data;
									data.multiArray[2] = [];
									for(let x in res.data.data){
										let district = res.data.data[x];
										data.multiArray[2].push(district.name);
									}
								
									data.multiIndex[2] = 0;
									
									that.regionArray = data.multiArray;
									that.regionIndex = data.multiIndex;
									that.regionData = data.multiData;
								}
							},
						});
					}
						break;
				}
				
			},
			
			goback:function(){
				uni.navigateBack({
					
				});
			},
			
			closeBuyLayer: function(){
				this.tobuy=false;
				var animation = uni.createAnimation({
					duration: 1000,
					timingFunction: 'ease-out',
				});
				this.tobuyAnimation = animation.export();
				
			}
			
		}
	}
</script>

